.login-form,
.login-form * {
  box-sizing: border-box;
  font-family: "Source Sans Pro";
}

.login-form {
  max-width: 350px;
  margin: 0 auto;
  border-radius: 5px;
  overflow: hidden;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.15);
  margin-top: 80px;

}

.login-form__content {
  padding: 30px;
  background: #eeeeee;
}

.login-form__header {
  margin-bottom: 15px;
  text-align: center;
  color: #333333;
}

.login-form__links {
  margin-top: 15px;
  text-align: center;
}

.login-form__link {
  font-size: 0.9em;
  color: #bb906d;
  text-decoration: none;
}

.a{
  font-size: 15px;
  color: #333333;
  transition: 2s;

}
.a:hover{
  font-size: 12px;
  color: white;
}



.login-form__input {
  width: 100%;
  margin-bottom: 10px;
  padding: 10px;
  border-radius: 5px;
  border: 2px solid #dddddd;
  background: #ffffff;
  outline: none;
  transition: border-color 0.5s;
}

.login-form__input:focus {
  border-color: #e6bc98;
}

.login-form__input::-webkit-input-placeholder {
  color: #aaaaaa;
}

.login-form__input:-ms-input-placeholder {
  color: #aaaaaa;
}

.login-form__input::placeholder {
  color: #aaaaaa;
}

.form-button{
  padding: 10px;
  color: #ffffff;
  font-weight: bold;
  background: #bb906d;
  width: 100%;
  border: none;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
}

.form-button:active {
  background: #bb906d;
}

